import emailjs from '@emailjs/browser';

export function sendEmail(mensaje){
    
    const userID = 'Glib8bUkBPGAj9Lyp'
    const serviceID = 'service_m46n6mx'
    const templateID = 'template_rioc0yw'

    // Definir los parmetros de la plantilla
    const templateParams = {
        message: mensaje,
        // Agrega cualquier otro parmetro necesario segn la plantilla que ests utilizando
    };

    // Inicializar emailjs con tu User ID
    emailjs.init(userID);

    // Enviar el correo electrnico
    emailjs.send(serviceID, templateID, templateParams)
        .then((response) => {
            alert(`Informacin enviada con xito!\nRecibirs un mail de confirmacin.
Si quers mensajeame directo ---> Instragram: @juanitrovato`);
            console.log(response)
            window.location.href = '/';
        })
        .catch((error) => {
            alert('Error al enviar el correo electrnico.\nPor favor, intentalo de nuevo.');
            console.log(error)
            window.location.reload();
        });
}       
